import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Header from './Header';
import GlobalFeed from './GlobalFeed';
import Login from './Login';
import Signup from './Signup';
import Newpost from './Newpost';
import Me from './Me';
import Profile from './Profile';
import NotFound from './NotFound';

export default class App extends React.Component {
  constructor() {
    super();
    this.state = {
      islogged: false
    };
  }

  handleIsnav = () => {
    this.setState({ islogged: true });
  };

  publicRoutes() {
    return (
      <>
        <Switch>
          <Route exact path="/login">
            <Login handleIsnav={this.handleIsnav} />
          </Route>
          <Route exact path="/signup">
            <Signup handleIsnav={this.handleIsnav} />
          </Route>
          <Route exact path="/users/:profile">
            <Profile />
          </Route>
          <Route exact path="/">
            <GlobalFeed />
          </Route>
          <Route exact>
            <NotFound />
          </Route>
        </Switch>
      </>
    );
  }

  privateRoutes() {
    return (
      <>
        <Switch>
          <Route exact path="/create">
            <Newpost />
          </Route>
          <Route exact path="/me">
            <Me />
          </Route>
          <Route exact path="/users/:profile">
            <Profile />
          </Route>
          <Route exact path="/">
            <GlobalFeed />
          </Route>
          <Route exact>
            <NotFound />
          </Route>
        </Switch>
      </>
    );
  }

  render() {
    return (
      <>
        <Header isnav={this.state.islogged} handleIsnav={this.handleIsnav} />

        {localStorage.altmagzloggedUser
          ? this.privateRoutes()
          : this.publicRoutes()}
      </>
    );
  }
}
