import React from 'react';
import { withRouter } from 'react-router-dom';

class Newpost extends React.Component {
  constructor() {
    super();
    this.state = {
      title: '',
      description: '',
      body: ''
    };
  }

  handleChange = e => {
    let value = e.target.value;
    let name = e.target.name;
    this.setState({ [name]: value });
  };

  handleSubmit = e => {
    e.preventDefault();
    fetch('https://altcampusmagz.herokuapp.com/api/articles', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: localStorage.altmagzloggedUser
      },
      body: JSON.stringify({
        title: this.state.title,
        description: this.state.description,
        body: this.state.body
      })
    })
      .then(res => res.json())
      .then(data => {
        if (data._id) {
          this.props.history.push('/');
        } else {
          alert('enter valid information to publish');
        }
      });
  };

  render() {
    return (
      <>
        <div className="signup">
          <div className="signup-wrapper">
            <h1 className="signup-text">Create a New Post</h1>
            <form>
              <input
                className="signup-input"
                type="text"
                required
                name="title"
                placeholder="Article title"
                value={this.state.title}
                onChange={this.handleChange}
              />
              <input
                className="signup-input"
                type="text"
                required
                name="description"
                placeholder="Article description"
                value={this.state.description}
                onChange={this.handleChange}
              />
              <input
                className="signup-input"
                type="text"
                required
                name="body"
                placeholder="Add the link of the article"
                value={this.state.body}
                onChange={this.handleChange}
              />
              <input
                className="signup-btn"
                type="submit"
                value="Publish"
                onClick={this.handleSubmit}
              />
            </form>
          </div>
        </div>
      </>
    );
  }
}

export default withRouter(Newpost);
