import React from 'react';
import { NavLink } from 'react-router-dom';
import GlobalFeed from './GlobalFeed';

function PublicNav() {
  return (
    <div className="nav-items-right">
      <NavLink className="nav-item" to="/signup">
        signup
      </NavLink>
      <NavLink className="nav-item" to="/login">
        Login
      </NavLink>
    </div>
  );
}

function handleLogout(handleIsnav) {
  localStorage.removeItem('altmagzusername');
  localStorage.removeItem('altmagzloggedUser');
  handleIsnav();
}

function PrivateNav(handleIsnav) {
  return (
    <div>
      <NavLink className="nav-item" to="/create">
        New Post
      </NavLink>
      <NavLink className="nav-item" to="/me">
        Me
      </NavLink>
      <NavLink
        onClick={() => handleLogout(handleIsnav)}
        to="/"
        className="nav-item"
      >
        Logout
      </NavLink>
    </div>
  );
}

export default function Header(props) {
  return (
    <>
      <div className="header">
        <div className="header-wrapper">
          <div className="nav-bar">
            <div>
              <NavLink className="nav-logo" exact to="/">
                AltMagz
              </NavLink>
            </div>
            <div className="footer rfooter">
              <p className="footer-text">Developed by </p>

              <a
                title="chaduvula prasanth"
                href="https://twitter.com/chaduvula98"
                target="_blank"
              >
                <p className="footer-text-name">Chaduvula Prasanth</p>
              </a>
              <a
                className="footer-link"
                href="https://altcampus.io/"
                target="_blank"
              >
                Made with ❤️ in{' '}
                <span className="footer-text-name">Altcampus</span>
              </a>
            </div>
            {localStorage.altmagzloggedUser
              ? PrivateNav(props.handleIsnav)
              : PublicNav()}
          </div>
        </div>
      </div>
    </>
  );
}
