import React from 'react';
import { NavLink, withRouter } from 'react-router-dom';

class GlobalFeed extends React.Component {
  constructor() {
    super();
    this.state = {
      articles: []
    };
  }
  componentDidMount() {
    fetch('https://altcampusmagz.herokuapp.com/api/articles/')
      .then(res => res.json())
      .then(data => {
        this.setState({ articles: data.articles.reverse() });
      });
  }

  render() {
    return (
      <>
        <div className="feed">
          <div className="feed-wrapper">
            {this.state.articles.map(article => (
              <div className="post-card">
                <h2 className="post-title">{article.title}</h2>
                <NavLink to={`/users/${article.userId.username}`}>
                  <div className="post-profile">
                    <img
                      className="post-profile-pic"
                      src={`${article.userId.picture}`}
                      alt=""
                    />
                    <div className="post-author-div">
                      <p className="post-author">Author</p>
                      <p className="post-author-name">
                        {article.userId.username}
                      </p>
                    </div>
                    <div>
                      <p className="post-type">Published</p>
                      <p className="post-time">
                        {new Intl.DateTimeFormat('en-GB', {
                          month: 'long',
                          day: '2-digit',
                          year: 'numeric'
                        }).format(new Date(article.createdAt))}
                      </p>
                    </div>
                  </div>
                </NavLink>
                <p className="post-descri">{article.description}</p>
                <a
                  className="post-readmore"
                  href={`${article.body}`}
                  target="_blank"
                >
                  Read More
                </a>
              </div>
            ))}
          </div>
        </div>
      </>
    );
  }
}
export default withRouter(GlobalFeed);
