import React from 'react';
import { withRouter } from 'react-router-dom';

class Login extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  handleChange = e => {
    let value = e.target.value;
    let name = e.target.name;
    this.setState({ [name]: value });
  };

  handleSubmit = e => {
    e.preventDefault();
    fetch('https://altcampusmagz.herokuapp.com/api/users/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        email: this.state.email,
        password: this.state.password
      })
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) {
          localStorage.setItem('altmagzloggedUser', data.token);
          localStorage.setItem('altmagzusername', data.username);
          this.props.handleIsnav();
          this.props.history.push('/');
        } else {
          alert('enter valid details');
        }
      });
  };

  render() {
    return (
      <>
        <div className="signup">
          <div className="signup-wrapper">
            <h1 className="signup-text">Login</h1>
            <form>
              <input
                className="signup-input"
                type="email"
                required
                name="email"
                placeholder="enter your email"
                value={this.state.email}
                onChange={this.handleChange}
              />
              <input
                className="signup-input"
                type="password"
                required
                name="password"
                placeholder="enter your password"
                value={this.state.password}
                onChange={this.handleChange}
              />

              <input
                className="signup-btn"
                type="submit"
                value="Login"
                onClick={this.handleSubmit}
              />
            </form>
          </div>
        </div>
      </>
    );
  }
}

export default withRouter(Login);
