import React from 'react';
import { withRouter } from 'react-router-dom';

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: ''
    };
  }

  componentDidMount() {
    const profile = this.props.match.params.profile;
    if (localStorage.altmagzusername == profile) {
      this.props.history.push('/me');
    } else {
      fetch(`https://altcampusmagz.herokuapp.com/api/profiles/${profile}`)
        .then(res => res.json())
        .then(data =>
          this.setState({
            user: data.user
          })
        );
    }
  }
  render() {
    return (
      <>
        <div className="profile">
          <div className="profile-wrapper">
            <div className="profile-details">
              <img
                className="profile-pic"
                src={`${this.state.user.picture}`}
                alt=""
              />
              <p className="profile-name">{this.state.user.username}</p>
            </div>
          </div>
          <div className="feed-wrapper">
            {this.state.user &&
              this.state.user.articlesId.reverse().map(article => (
                <div className="post-card">
                  <h2 className="post-title">{article.title}</h2>
                  <div className="post-profile">
                    <img
                      className="post-profile-pic"
                      src={`${this.state.user.picture}`}
                      alt=""
                    />
                    <div className="post-author-div">
                      <p className="post-author">Author</p>
                      <p className="post-author-name">
                        {this.state.user.username}
                      </p>
                    </div>
                    <div>
                      <p className="post-type">Published</p>
                      <p className="post-time">
                        {new Intl.DateTimeFormat('en-GB', {
                          month: 'long',
                          day: '2-digit',
                          year: 'numeric'
                        }).format(new Date(article.createdAt))}
                      </p>
                    </div>
                  </div>
                  <p className="post-descri">{article.description}</p>
                  <a
                    className="post-readmore"
                    href={`${article.body}`}
                    target="_blank"
                  >
                    Read More
                  </a>
                </div>
              ))}
          </div>
        </div>
      </>
    );
  }
}

export default withRouter(Profile);
